
.footer_container{
    margin: 200px 0 0 0;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
}

.footer_parent{
    width: 100%;
    height: 100%;
}

.footer_parent img{
    width: 100%;
    height: 130px;
}

@media only screen and (max-width: 1110px){
    .footer_parent{
        display: flex;
        align-items: flex-end;
    }
}