.profile_container{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 10%;
    min-height: fit-content;
}

.profile_parent{
    display: flex;
    align-items: center;
    color: aliceblue;
}

.profile_details_name{
    font-size: 24px;
    font-family: "Poppins SemiBold";
}

.profile_details_role{
    display: flex;
    flex-direction: column;
    margin: 14px 0 24px 0px;
}

.profile_details_role h1 {
    font-size: 35px;
    font-family: "cursive";
}

.primary_text{
    color: (--white);
}

.highlighted_text{
    color: var(--dark-orange);
}

.profile_role_tagline{
    font-size: 19px;
    margin: 5px 0 0 0;
    font-family: "Poppins Light";
}

.profile_options .highlighted_button {
    margin: 0 0 0 28px;
}

.profile_options .highlighted_button:hover {
    margin: 0 0 0 28px;
    background-color: cornsilk;
    color: #1f2235; 
}

.profile_picture{
    display:flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 1px 0 0 var(--white);
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 0px 0px 0px 100px;
}

.profile_picture_background {
    height: 92%;
    width: 92%;
    background-image: url('../../../assets/Home/Profile_picture.jpg');
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}

.profile_picture_background:hover {
    transform: scale(1.07);
    transition: 1s ease-out;
}

.fa-linkedin-square{
    color: white;
    font-size: 50px;
    padding: 8px;
    padding-left: 0px;
}

.fa-github{
    color: white;
    font-size: 50px;
    padding: 8px;
    padding-left: 0px;
}

.icons .icon_list{
    padding: 8px;
    padding-left: 0px;
}

.icons .icon_list i:hover{
    transform: scale(1.5);
}

@media screen and (max-width: 1110px){
    .profile_picture{
        margin: 0 0x 0 80px;
    }

    .profile_details_name{
        font-size: 20px;
    }

    .profile_details_role .primary_text{
        font-size: 12px;
    }

    .profile_options .button{
        width: 120px;
        padding: 11px 0;
        z-index: 700;
    }
}

@media screen and (max-width: 910px){
    .profile_picture{
        height: 320px;
        width: 320px;
        margin: 0 0 0 4px;
    }
}

@media screen and (max-width: 810px){
    .profile_picture{
        margin: 0 !important;
    }
    .profile_parent{
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }
    .profile_options{
        display: flex;
        justify-content: space-between;
    }
    .profile_details{
        margin: 25px 0 0;
    }
    .profile_options .highlighted_button {
        margin: 0 !important;
    }
    .profile_options,
    .profile_details_role {
        text-align: center;
    }

}

@media screen and (max-width: 375px){
    .profile_picture{
        height: 275px;
        width: 275px;
        margin-top: -90px;
    }
    .profile_details{
        width: 90%;
    }

    .fa-linkedin-square{
        color: royalblue;
        font-size: 50px;
        padding: 8px;
        padding-left: 0px;
    }
    
    .fa-github{
        color: white;
        font-size: 50px;
        padding: 8px;
        padding-left: 0px;
        border-radius: 2px;
        border-color: black;
    }
}



